.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  overflow: hidden;
  padding: 5.5rem 0 2rem 0;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><radialGradient id="a" cx="50%" cy="50%"><stop offset="0%" stop-color="%23ffffff" stop-opacity="0.1"/><stop offset="100%" stop-color="%23ffffff" stop-opacity="0"/></radialGradient></defs><circle cx="200" cy="200" r="100" fill="url(%23a)"/><circle cx="800" cy="300" r="150" fill="url(%23a)"/><circle cx="400" cy="700" r="120" fill="url(%23a)"/><circle cx="900" cy="800" r="80" fill="url(%23a)"/></svg>');
  opacity: 0.3;
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem;
  align-items: center;
  position: relative;
  z-index: 2;
}

.hero-text {
  color: white;
}

.hero-title {
  font-size: clamp(2.2rem, 4vw, 3.5rem);
  font-weight: 900;
  margin-bottom: 1rem;
  line-height: 1.1;
  background: linear-gradient(135deg, #ffffff 0%, #f0f9ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  background: linear-gradient(135deg, #06b6d4 0%, #3b82f6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-description {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
  line-height: 1.6;
  max-width: 500px;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.hero-buttons .btn {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  font-weight: 600;
  padding: 1rem 2rem;
  border-radius: 50px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-buttons .btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.hero-buttons .btn-outline {
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
}

.hero-buttons .btn-outline:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.8);
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.profile-container {
  position: relative;
  width: 300px;
  height: 300px;
}

.profile-circle {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: transparent;
  backdrop-filter: blur(20px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: float 6s ease-in-out infinite;
  box-shadow: 
    0 0 0 20px rgba(255, 255, 255, 0.05),
    0 0 0 40px rgba(255, 255, 255, 0.03),
    0 0 0 60px rgba(255, 255, 255, 0.01);
  overflow: hidden;
  position: relative;
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
  z-index: 2;
}

.profile-circle::before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  border-radius: 50%;
  background: conic-gradient(from 0deg, #06b6d4, #3b82f6, #8b5cf6, #ec4899, #06b6d4);
  animation: rotate 10s linear infinite;
  z-index: -1;
}

.floating-elements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.floating-element {
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: float 8s ease-in-out infinite;
}

.floating-element:nth-child(1) {
  width: 20px;
  height: 20px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.floating-element:nth-child(2) {
  width: 15px;
  height: 15px;
  top: 60%;
  right: 20%;
  animation-delay: 2s;
}

.floating-element:nth-child(3) {
  width: 25px;
  height: 25px;
  bottom: 30%;
  left: 20%;
  animation-delay: 4s;
}

.social-links {
  position: fixed;
  left: 2rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 100;
}

.social-links a {
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.social-links a:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-5px) scale(1.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

/* Technology Logos */
.tech-logos {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 2rem;
  z-index: 10;
}

.tech-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;
}

.tech-logo::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tech-logo:hover::before {
  opacity: 1;
  animation: shimmer 1.5s ease-in-out infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.tech-logo svg {
  font-size: 2.5rem;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 2;
  position: relative;
}

/* Individual Logo Styling */
.aws-logo {
  background: linear-gradient(135deg, #FF9900 0%, #FF6600 100%);
  box-shadow: 
    0 8px 32px rgba(255, 153, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.aws-logo:hover {
  box-shadow: 
    0 12px 40px rgba(255, 153, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.react-logo {
  background: linear-gradient(135deg, #61DAFB 0%, #21D4FD 100%);
  box-shadow: 
    0 8px 32px rgba(97, 218, 251, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.react-logo:hover {
  box-shadow: 
    0 12px 40px rgba(97, 218, 251, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.node-logo {
  background: linear-gradient(135deg, #339933 0%, #2E7D32 100%);
  box-shadow: 
    0 8px 32px rgba(51, 153, 51, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.node-logo:hover {
  box-shadow: 
    0 12px 40px rgba(51, 153, 51, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.mongo-logo {
  background: linear-gradient(135deg, #47A248 0%, #388E3C 100%);
  box-shadow: 
    0 8px 32px rgba(71, 162, 72, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.mongo-logo:hover {
  box-shadow: 
    0 12px 40px rgba(71, 162, 72, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.robot-logo {
  background: linear-gradient(135deg, #412991 0%, #6A1B9A 100%);
  box-shadow: 
    0 8px 32px rgba(65, 41, 145, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.robot-logo:hover {
  box-shadow: 
    0 12px 40px rgba(65, 41, 145, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* Glow Effects */
.aws-logo svg {
  color: white;
  filter: drop-shadow(0 0 8px rgba(255, 153, 0, 0.6));
}

.react-logo svg {
  color: white;
  filter: drop-shadow(0 0 8px rgba(97, 218, 251, 0.6));
}

.node-logo svg {
  color: white;
  filter: drop-shadow(0 0 8px rgba(51, 153, 51, 0.6));
}

.mongo-logo svg {
  color: white;
  filter: drop-shadow(0 0 8px rgba(71, 162, 72, 0.6));
}

.robot-logo svg {
  color: white;
  filter: drop-shadow(0 0 8px rgba(65, 41, 145, 0.6));
}

@media (max-width: 1366px) {
  .hero-content {
    gap: 2.5rem;
  }
  
  .hero-text h1 {
    font-size: clamp(2.2rem, 3.5vw, 3rem);
    margin-bottom: 1rem;
  }
  
  .hero-text p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
  }
  
  .profile-container {
    width: 320px;
    height: 320px;
  }
  
  .tech-logos {
    bottom: 2rem;
    gap: 1.5rem;
  }
  
  .tech-logo {
    width: 50px;
    height: 50px;
  }
  
  .tech-logo svg {
    font-size: 2rem;
  }
}

@media (max-width: 1024px) {
  .hero {
    min-height: 80vh;
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 3rem;
  }
  
  .profile-container {
    width: 300px;
    height: 300px;
  }
  
  .hero-text h1 {
    font-size: clamp(2rem, 5vw, 3rem);
  }
  
  .hero-text p {
    font-size: 1rem;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .hero-buttons {
    justify-content: center;
    gap: 1rem;
  }
  
  .social-links {
    position: static;
    flex-direction: row;
    justify-content: center;
    margin-top: 2rem;
    gap: 1.5rem;
  }
  
  .tech-logos {
    position: static;
    transform: none;
    margin-top: 2rem;
    justify-content: center;
  }
  
  .tech-logo {
    width: 50px;
    height: 50px;
  }
  
  .tech-logo svg {
    font-size: 2rem;
  }
}

@media (max-width: 768px) {
  .hero {
    min-height: 100vh;
    padding: 4.5rem 0 2rem 0;
  }
  
  .hero-content {
    gap: 2rem;
  }
  
  .profile-container {
    width: 250px;
    height: 250px;
  }
  
  .hero-text h1 {
    font-size: clamp(1.8rem, 6vw, 2.5rem);
    line-height: 1.2;
  }
  
  .hero-text p {
    font-size: 0.95rem;
    margin-bottom: 1.5rem;
  }
  
  .hero-buttons {
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .btn {
    width: 100%;
    max-width: 250px;
  }
  
  .social-links {
    position: static;
    flex-direction: row;
    justify-content: center;
    margin-top: 3rem;
    transform: none;
    gap: 1rem;
  }
  
  .tech-logos {
    position: static;
    transform: none;
    margin-top: 1.5rem;
    justify-content: center;
    gap: 1rem;
  }
  
  .tech-logo {
    width: 45px;
    height: 45px;
  }
  
  .tech-logo svg {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .hero {
    min-height: 100vh;
    padding: 4rem 0 1.5rem 0;
  }
  
  .hero-content {
    gap: 1.5rem;
  }
  
  .profile-container {
    width: 200px;
    height: 200px;
  }
  
  .hero-text h1 {
    font-size: clamp(1.5rem, 7vw, 2rem);
    margin-bottom: 1rem;
  }
  
  .hero-text p {
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }
  
  .hero-buttons {
    gap: 0.75rem;
  }
  
  .btn {
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
  }
  
  .social-links {
    gap: 0.75rem;
  }
  
  .tech-logos {
    position: static;
    transform: none;
    margin-top: 1rem;
    justify-content: center;
    gap: 0.75rem;
    flex-wrap: wrap;
  }
  
  .tech-logo {
    width: 40px;
    height: 40px;
  }
  
  .tech-logo svg {
    font-size: 1.5rem;
  }
}
