.skills {
  background: var(--bg-secondary);
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="hexagons" width="60" height="52" patternUnits="userSpaceOnUse"><polygon points="30,2 58,15 58,37 30,50 2,37 2,15" fill="none" stroke="%236366f1" stroke-width="1" opacity="0.1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23hexagons)"/></svg>');
  opacity: 0.3;
}

/* Tab Navigation */
.skills-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  margin-bottom: 2rem;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: var(--bg-primary);
  border: 2px solid var(--border-color);
  border-radius: 50px;
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.tab-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s ease;
}

.tab-button:hover::before {
  left: 100%;
}

.tab-button:hover {
  background: var(--bg-secondary);
  border-color: var(--primary-color);
  color: var(--text-primary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.tab-button.active {
  background: var(--gradient-primary);
  border-color: var(--primary-color);
  color: white;
  box-shadow: var(--shadow-lg);
}

.tab-button.active::before {
  display: none;
}

.tab-emoji {
  font-size: 1.1rem;
  transition: transform 0.3s ease;
}

.tab-button:hover .tab-emoji {
  transform: scale(1.2);
}

.tab-title {
  white-space: nowrap;
  transition: all 0.3s ease;
}

/* Tab Content */
.tab-content {
  position: relative;
  z-index: 2;
  min-height: 400px;
}

.skills-grid {
  display: flex;
  gap: 1.5rem;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 1rem 0;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--bg-secondary);
  white-space: nowrap;
}

.skills-grid::-webkit-scrollbar {
  height: 12px;
}

.skills-grid::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 6px;
  margin: 0 1rem;
}

.skills-grid::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 6px;
  transition: background 0.3s ease;
  border: 2px solid var(--bg-secondary);
}

.skills-grid::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}

.skill-item {
  background: var(--bg-primary);
  border-radius: 16px;
  padding: 1.5rem;
  text-align: center;
  border: 1px solid var(--border-color);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  min-width: 220px;
  flex-shrink: 0;
}

.skill-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 16px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.skill-item:hover::before {
  opacity: 1;
}

.skill-item:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: var(--primary-color);
}

.skill-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
  position: relative;
  z-index: 2;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1) rotate(5deg);
}

.skill-info {
  position: relative;
  z-index: 2;
}

.skill-info h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
}

.skill-level {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.skill-bar {
  width: 100%;
  height: 6px;
  background: var(--bg-tertiary);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
  transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.skill-progress::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.skill-percentage {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-align: center;
}

/* Responsive Design */
@media (max-width: 1366px) {
  .skills-tabs {
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .tab-button {
    padding: 0.6rem 1rem;
    font-size: 0.85rem;
  }
  
  .tab-emoji {
    font-size: 1rem;
  }
  
  .skills-grid {
    gap: 1.25rem;
  }
  
  .skill-item {
    padding: 1.25rem;
    min-width: 200px;
  }
  
  .skill-icon {
    font-size: 2.2rem;
  }
  
  .skill-info h4 {
    font-size: 0.95rem;
  }
}

@media (max-width: 1024px) {
  .skills-tabs {
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .tab-button {
    padding: 0.6rem 0.9rem;
    font-size: 0.8rem;
  }
  
  .skills-grid {
    gap: 1rem;
  }
  
  .skill-item {
    padding: 1rem;
    min-width: 180px;
  }
  
  .skill-icon {
    font-size: 2rem;
  }
  
  .skill-info h4 {
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .skills-tabs {
    gap: 0.4rem;
    margin-bottom: 1.25rem;
    justify-content: flex-start;
    overflow-x: auto;
    padding-bottom: 0.5rem;
    scrollbar-width: thin;
    scrollbar-color: var(--primary-color) var(--bg-secondary);
  }
  
  .skills-tabs::-webkit-scrollbar {
    height: 4px;
  }
  
  .skills-tabs::-webkit-scrollbar-track {
    background: var(--bg-secondary);
    border-radius: 2px;
  }
  
  .skills-tabs::-webkit-scrollbar-thumb {
    background: var(--primary-color);
    border-radius: 2px;
  }
  
  .tab-button {
    padding: 0.5rem 0.8rem;
    font-size: 0.75rem;
    flex-shrink: 0;
  }
  
  .tab-emoji {
    font-size: 0.9rem;
  }
  
  .skills-grid {
    gap: 0.875rem;
  }
  
  .skill-item {
    padding: 0.875rem;
    min-width: 160px;
  }
  
  .skill-icon {
    font-size: 1.8rem;
  }
  
  .skill-info h4 {
    font-size: 0.85rem;
  }
  
  .skill-percentage {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .skills-tabs {
    gap: 0.3rem;
    margin-bottom: 1rem;
  }
  
  .tab-button {
    padding: 0.4rem 0.6rem;
    font-size: 0.7rem;
  }
  
  .tab-emoji {
    font-size: 0.8rem;
  }
  
  .skills-grid {
    gap: 0.75rem;
  }
  
  .skill-item {
    padding: 0.75rem;
    min-width: 140px;
  }
  
  .skill-icon {
    font-size: 1.6rem;
  }
  
  .skill-info h4 {
    font-size: 0.8rem;
  }
  
  .skill-percentage {
    font-size: 0.75rem;
  }
}

/* Auto-scrolling Skills Carousel */
.skills-carousel {
  margin-top: 1rem;
  padding: 1rem 0;
  position: relative;
  overflow: hidden;
}


.carousel-track {
  display: flex;
  gap: 2rem;
  animation: scroll-left 30s linear infinite;
  width: fit-content;
}

.carousel-item {
  font-size: 2.5rem;
  padding: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel-item:hover {
  transform: scale(1.2) rotate(10deg);
}

@keyframes scroll-left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Pause animation on hover */
.skills-carousel:hover .carousel-track {
  animation-play-state: paused;
}

/* Responsive Design for Carousel */
@media (max-width: 1366px) {
  .skills-carousel {
    margin-top: 0.75rem;
    padding: 0.75rem 0;
  }
  
  .carousel-track {
    gap: 1.5rem;
  }
  
  .carousel-item {
    font-size: 2.2rem;
    padding: 0.4rem;
  }
}

@media (max-width: 1024px) {
  .skills-carousel {
    margin-top: 0.5rem;
    padding: 0.625rem 0;
  }
  
  .carousel-track {
    gap: 1.25rem;
  }
  
  .carousel-item {
    font-size: 2rem;
    padding: 0.35rem;
  }
}

@media (max-width: 768px) {
  .skills-carousel {
    margin-top: 0.5rem;
    padding: 0.5rem 0;
  }
  
  .carousel-track {
    gap: 1rem;
  }
  
  .carousel-item {
    font-size: 1.8rem;
    padding: 0.3rem;
  }
}

@media (max-width: 480px) {
  .skills-carousel {
    margin-top: 0.5rem;
    padding: 0.4rem 0;
  }
  
  .carousel-track {
    gap: 0.875rem;
  }
  
  .carousel-item {
    font-size: 1.6rem;
    padding: 0.25rem;
  }
}